<template>
  <v-container>
    <p>{{ longText }}</p>
    <v-row>
      <v-col cols="12" md="6">
        <v-text-field
          v-model="searchProject"
          label="Enter project name"
          outlined
        />
      </v-col>
      <v-col cols="12" md="6">
        <v-btn @click="fetchLeads" color="primary">Search</v-btn>
      </v-col>
    </v-row>

    <v-alert v-if="loading" type="info" dismissible>
      Loading...
    </v-alert>
    
    <v-alert v-if="error" type="error" dismissible>
      An error occurred: {{ error.message }}
    </v-alert>

    <v-data-table
      v-if="!loading && !error && leads.length"
      :headers="headers"
      :items="leads"
      item-key="id"
      class="elevation-1"
    >
      <template v-slot:[`item.name`]="{ item }">
        {{ item.attributes.name }}
      </template>
      <template v-slot:[`item.phoneNumber`]="{ item }">
        {{ item.attributes.phoneNumber }}
      </template>
    </v-data-table>
  </v-container>
</template>

<script>
import gql from 'graphql-tag';
import { ref } from 'vue';
import { useQuery } from '@vue/apollo-composable';

export default {
  name: 'searchList',
  data() {
    return {
      longText: `Previously you asked us about Ashwood newly launched condo at jalan uthan, 5 min to KLCC, 10 minute to TRX, opposite to flose international school, uthant street, next to china embassy, and sayfol international school, opposite the street is SJKK Chung Hwa(p) School, this Saturday we 
      have a priivate events, we have luycky draw, left 30 lot, all unit are cornet unit, all have open view to KLCC or bukit Ampang, 
      are you interested for own stay or investment, cn you recommend me some customer we have reseller program, if we close deal, 
      we will give you referal fees. ` +
      `Hi xxx, how re you today, I understand that you have a unit at un height, is it for rent or for sales, 
      we will give you free advertise to help you generate income for your unit`
      
    };
  },
  setup() {
    const searchProject = ref('');
    const leads = ref([]);
    const loading = ref(false);
    const error = ref(null);

    const headers = [
      { text: 'Name', value: 'name' },
      { text: 'Phone Number', value: 'phoneNumber' },
    ];

    const GET_LEADS_BY_PROJECT = gql`
      query($project: String!) {
        leads(filters: { project: { eq: $project } }, pagination: { limit: 1000 }) {
          data {
            id
            attributes {
              name
              phoneNumber
              project
              user
              status
              phoneId
              interaction
              lastCall
              nextCall
              called
              contacted
              language
              message
              doc
              na1
              source
              occupation
              updatedAt
            }
          }
        }
      }
    `;

    const fetchLeads = async () => {
      loading.value = true;
      error.value = null;

      try {
        const { result, error: queryError } = useQuery(GET_LEADS_BY_PROJECT, {
          project: searchProject.value,
        });

        if (queryError.value) {
          error.value = queryError.value;
          console.error('Error fetching leads:', error.value);
        } else if (result.value) {
          leads.value = result.value.leads.data;
        }
      } catch (e) {
        error.value = e;
        console.error('Unexpected error:', e);
      } finally {
        loading.value = false;
      }
    };

    return {
      searchProject,
      leads,
      fetchLeads,
      loading,
      error,
      headers,
    };
  },
};
</script>

<style scoped>
